import React from "react";
import {
  Form,
  FormGroup,
  Label,
  Input,
  Col,
  FormFeedback,
  Button,
  CustomInput
} from "reactstrap";
import { withRouter } from "react-router-dom";

class Settings extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      firstname: null,
      lastname: null,
      phrase: null,
      status: null
    };
    this.handleSubmit = this.handleSubmit.bind(this);
    this.controlState = this.controlState.bind(this);
  }
componentDidMount(){
  this.setState({
    status: this.props.user.user.publicStatus
  });
}
  controlState(e) {
    const target = e.target;
    const value = target.value;
    const name = target.name;
    if(name === "status"){
      this.setState({
        status: !this.state.status
      });
    }else{
          this.setState({
      [name]: value
    });
    }

  }

  handleSubmit(e) {
    e.preventDefault();
    const settings = {
      firstname: this.state.firstname,
      lastname: this.state.lastname,
      phrase: this.state.phrase,
      status: this.state.status
    };
    const userid = this.props.user.user._id;

    this.props.settingsUser(userid, settings).then(() => {
      this.props.history.push("/home");
      window.location.reload(true);
    });
  }

  render() {
    console.log(this.state);
    return (
      <div>
        <div className="container ">
          <Form onSubmit={this.handleSubmit} encType="multipart/form-data">
            <FormGroup row>
              <Label htmlFor="firstname" md={2}>
                Nombre
              </Label>
              <Col md={10}>
                <Input
                  type="text"
                  id="firstname"
                  name="firstname"
                  placeholder="Nombre"
                  onChange={this.controlState}
                  value={this.state.firstname}
                />
                <FormFeedback />
              </Col>
            </FormGroup>
            <FormGroup row>
              <Label htmlFor="lastname" md={2}>
                Apellido
              </Label>
              <Col md={10}>
                <Input
                  type="text"
                  id="lastname"
                  name="lastname"
                  placeholder="Apellido"
                  onChange={this.controlState}
                  value={this.state.lastname}
                />
                <FormFeedback />
              </Col>
            </FormGroup>
            <FormGroup row>
              <Label htmlFor="phrase" md={2}>
                Frase de Cabecera
              </Label>
              <Col md={10}>
                <Input
                  type="textarea"
                  id="phrase"
                  name="phrase"
                  placeholder="Frase de Cabecera"
                  onChange={this.controlState}
                  value={this.state.phrase}
                />
              </Col>
            </FormGroup>
            <FormGroup row>
              <Label htmlFor="status" md={2}>
                Estado de la cuenta
              </Label>
              <Col md={10}>
                {this.state.status === true ?
                <CustomInput type="switch" id="status" name="status" label="privada" 
                onChange={this.controlState}
                value={this.state.status}
                checked
                />
                :
                <CustomInput type="switch" id="status" name="status" label="publica" 
                onChange={this.controlState}
                value={this.state.status}
                
                />
                }
              
              </Col>
              <Col>
                <Button type="submit">Enviar</Button>
              </Col>
            </FormGroup>
          </Form>
        </div>
      </div>
    );
  }
}

export default withRouter(Settings);
