import * as ActionTypes from "./ActionTypes";
import { baseUrl } from "../shared/baseUrl";

//VIEW AFTER LOGIN
export const fetchStart = () => (dispatch) => {
  dispatch(startLoading());
  const bearer = 'Bearer ' + localStorage.getItem('token');
  const id = JSON.parse(localStorage.getItem('id'))
  return fetch(baseUrl + `start/publications/${id}`, {
    method: "GET",
    headers: {
      'Authorization': bearer
    },
  })
    .then(response => {
      if (response.ok) {
        return response;

      }
      else {
        var error = new Error('Error ' + response.status + ': ' + response.statusText);
        error.response = response;
        throw error;
      }
    },
      error => {
        var errmess = new Error(error.message);
        throw errmess;
      })
    .then(response => response.json())
    .then(start => dispatch(addStart(start)))
    .then(start => dispatch(inboxFetch()))
    .catch(error => dispatch(startFailed(error.message)));
}
export const startLoading = () => ({
  type: ActionTypes.START_LOADING
});

export const startFailed = (errmess) => ({
  type: ActionTypes.START_FAILED,
  payload: errmess
});

export const addStart = (start) => ({
  type: ActionTypes.START_ADD,
  payload: start
});


//REGISTER POST DATA
export const signupUser = User => dispatch => {
  const newUser = {
    username: User.username,
    password: User.password,
    date: User.date,
    sex: User.sex,
    country: User.country
  };
 
  return fetch(baseUrl + "users/signup", {
    method: "POST",
    body: JSON.stringify(newUser),
    headers: {
      "Content-Type": "application/json"
    },
    credentials: "same-origin"
  })
    .then(
      response => {
        if (response.ok) {
          return response;
        } else {
          var error = new Error(
            "Error " + response.status + ": " + response.statusText
          );
          error.response = response;
          throw error;
        }
      },
      error => {
        var errmess = new Error(error.message);
        throw errmess;
      }
    )
    .then(response => response.json())
    .then(response => {
      const Resp = response.status;
      dispatch(responseSignup(Resp));
    })
    .catch(error => {
      const Err = error.status;
      dispatch(errorSignup(Err));
    });
};
export const responseSignup = creds => {
  return {
    type: ActionTypes.SIGNUP_SUCCESS,
    payload: creds
  };
};
export const errorSignup = creds => {
  return {
    type: ActionTypes.SIGNUP_FAILURE,
    payload: creds
  };
};

export const requestLogin = creds => {
  return {
    type: ActionTypes.LOGIN_REQUEST,
    creds
  };
};
//se agrego userdata: response.user
export const receiveLogin = response => {
  return {
    type: ActionTypes.LOGIN_SUCCESS,
    token: response.token,
    userdata: response
  };
};

export const loginError = message => {
  return {
    type: ActionTypes.LOGIN_FAILURE,
    message
  };
};

export const loginUser = creds => dispatch => {
  // We dispatch requestLogin to kickoff the call to the API
  dispatch(requestLogin(creds));

  return fetch(baseUrl + "users/login", {
    method: "POST",
    headers: {
      "Content-Type": "application/json"
    },
    body: JSON.stringify(creds)
  })
    .then(
      response => {
        if (response.ok) {
          return response;
        } else {
          var error = new Error(
            "Error " + response.status + ": " + response.statusText
          );
          error.response = response;
          throw error;
        }
      },
      error => {
        throw error;
      }
    )
    .then(response => response.json())
    .then(response => {
      if (response.success) {
        // If login was successful, set the token in local storage
        localStorage.setItem("token", response.token);
        localStorage.setItem("creds", JSON.stringify(creds));
        localStorage.setItem("id", JSON.stringify(response.userdata._id));
        localStorage.setItem("img", JSON.stringify(response.userdata.image.filename));

        // Dispatch the success action
        //dispatch(fetchFavorites());
        const id = response.userdata.username;
        dispatch(receiveLogin(response));
        dispatch(fetchUser(id));
        dispatch(fetchNotifications(id));
        dispatch(fetchFollowers());
        dispatch(fetchFollowing());
        dispatch(fetchStart());

      } else {
        var error = new Error("Error " + response.status);
        error.response = response;
        throw error;
      }
    })
    .catch(error => dispatch(loginError(error.message)));
};

