import React from "react";
import {
  Form,
  FormGroup,
  Col,
  Input,
  Label,
  Button,
  FormFeedback
} from "reactstrap";
import { withRouter } from "react-router-dom";
import { countries } from '../shared/countries';

class Forms extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      username: "",
      password: "",
      repeatpassword: "",
      sex: "",
      date: "",
      country: "",
      touched: {
        password: false,
        username: false
      }
    };

    this.controlState = this.controlState.bind(this);
    this.handleBlur = this.handleBlur.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
  }

  controlState(e) {
    const target = e.target;
    const value = target.value;
    const name = target.name;

    this.setState({
      [name]: value
    });
  }

  handleBlur = field => e => {
    this.setState({
      touched: { ...this.state.touched, [field]: true }
    });
  };

  validar(password, repeatpassword, username, sex, date, country) {
    const error = {
      password: { err: "", valid: false },
      repeatpassword: { err: "", valid: false },
      username: { err: "", valid: false },
      sex: false,
      date: false,
      country: false
    };
    const expreg = /^(\w{3,})@(gmail|hotmail|outlook).\b(com|info|web)\b/;

    if (this.state.touched.password && password.length < 4) {
      error.password.err =
        "La contraseña es DEBIL. Debe ser mayor a 4 caracteres. Recomendamos alternar numeros y letras";

    } else if (this.state.touched.password && password.length > 10) {
      error.password.err =
        "El contraseña debe ser menor o igual a 10 caracteres. Recomendamos alternar numeros y letras";
    } else if (this.state.password !== "") {
      error.password.valid = true
    }

    if (this.state.touched.repeatpassword && repeatpassword !== this.state.password) {
      error.repeatpassword.err =
        "CONTRASEÑA Y REPETIR CONTRASEÑA NO COINCIDEN. POR FAVOR, VALIDA ESTE COMPO DE TEXTO";
    } else if (this.state.touched.repeatpassword && repeatpassword.length > 10) {
      error.repeatpassword.err =
        "El contraseña debe ser menor o igual a 10 caracteres. Recomendamos alternar numeros y letras";
    } else if (this.state.repeatpassword !== "") {
      error.repeatpassword.valid = true
    }
    if (this.state.touched.username && !expreg.test(username)) {
      error.username.err = "Debe cumplir el formato de email. Ej: exampe@mail.com";
    } else if (this.state.username !== "") {
      error.username.valid = true
    }

    if (sex !== "") {
      error.sex = true
    }
    if (date !== "") {
      error.date = true
    }
    if (country !== "") {
      error.country = true
    }
    return error;
  }
  enableForm(err) {
    var valid = false;
    if (
      err.password === "" && err.repeatpassword === "" && err.username === "" && err.valid
    )
      valid = true
    return valid
  }

  onSubmit = e => {
    e.preventDefault();
    // get form data out of state
    const User = {
      username: this.state.username,
      password: this.state.password,
      sex: this.state.sex,
      date: this.state.date,
      country: this.state.country
    };
    this.props.signupUser(User).then(() => {
      this.props.history.push("/users/post");
    });
  };

  render() {
    const error = this.validar(
      this.state.password,
      this.state.repeatpassword,
      this.state.username,
      this.state.sex,
      this.state.date,
      this.state.country
    );
    const enableButton = error.username.valid && error.password.valid &&
      error.repeatpassword.valid && error.sex &&
      error.date && error.country ? true : false;
    const country = countries.map((c, index) => <option key={index}>{c}</option>)
    return (
      <div>
        <div className="container-fluid">
          <div className=" col-sm-6 offset-sm-3">
            <h1>Crea una cuenta nueva</h1>
          </div>
        </div>

        <div className="container">
          <Form onSubmit={this.onSubmit}>
            <FormGroup row>
              <Label htmlFor="username" md={2}>
                Email
              </Label>
              <Col md={10}>
                <Input
                  type="email"
                  id="username"
                  name="username"
                  placeholder="example@mail.com"
                  value={this.state.username}
                  valid={error.username.err === ""}
                  invalid={error.username.err !== ""}
                  onChange={this.controlState}
                  onBlur={this.handleBlur("username")}
                />
                <FormFeedback>{error.username.err}</FormFeedback>
              </Col>
            </FormGroup>
            <FormGroup row>
              <Label htmlFor="password" md={2}>
                Contraseña
              </Label>
              <Col md={10}>
                <Input
                  type="password"
                  id="password"
                  name="password"
                  placeholder="password"
                  value={this.state.password}
                  valid={error.password.err === ""}
                  invalid={error.password.err !== ""}
                  onChange={this.controlState}
                  onBlur={this.handleBlur("password")}
                />
                <FormFeedback>{error.password.err}</FormFeedback>
              </Col>
            </FormGroup>
            <FormGroup row>
              <Label htmlFor="repeatpassword" md={2}>
                Repetir Contraseña
              </Label>
              <Col md={10}>
                <Input
                  type="password"
                  id="repeatpassword"
                  name="repeatpassword"
                  placeholder="repetir contraseña"
                  value={this.state.repeatpassword}
                  valid={error.repeatpassword.err === ""}
                  invalid={error.repeatpassword.err !== ""}
                  onChange={this.controlState}
                  onBlur={this.handleBlur("repeatpassword")}
                />
                <FormFeedback>{error.repeatpassword.err}</FormFeedback>
              </Col>
            </FormGroup>
            <FormGroup row>
              <Label htmlFor="sex" md={2}>
                Sex
              </Label>
              <Col md={10}>
                <Input
                  type="select"
                  id="sex"
                  name="sex"
                  value={this.state.sex}
                  onChange={this.controlState}
                >
                  <option selected>Female</option>
                  <option>Male</option>
                  <option>Trans</option>
                </Input>
              </Col>
            </FormGroup>
            <FormGroup row>
              <Label htmlFor="date" md={2}>
                Fecha de Naciminento
              </Label>
              <Col md={10}>
                <Input
                  type="date"
                  id="date"
                  name="date"
                  placeholder="Birth"
                  value={this.state.date}
                  onChange={this.controlState}
                />
              </Col>
            </FormGroup>
            <FormGroup row>
              <Label htmlFor="country" md={2}>
                Country
              </Label>
              <Col md={10}>
                <Input
                  type="select"
                  id="country"
                  name="country"
                  placeholder="country"
                  value={this.state.country}
                  onChange={this.controlState}
                >
                  {country}
                </Input>
              </Col>
              <Col>
                {
                  enableButton
                    ?
                    <Button type="file" >Enviar</Button>
                    :
                    <Button type="file" disabled>Enviar</Button>
                }

              </Col>
            </FormGroup>
          </Form>
        </div>
      </div>
    );
  }
}

export default withRouter(Forms);
