import React, { Component } from "react";
import Notifications from './NotificationsWindow'
import {
  Navbar,
  NavbarBrand,
  Nav,
  Collapse,
  NavItem,
  Form, FormGroup, Input, UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  NavbarToggler
} from "reactstrap";
import { NavLink } from "react-router-dom";
import { baseUrl } from '../shared/baseUrl';
import SearchRender from './SearchWindow';

class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      styled: "none",
      search: '',
      err: '',
      noti: '',
      inbox: [],
      readTrue: false,
      results: [],
      isNavOpen: false,
      viewMessage: ''
    }

    this.handleLogout = this.handleLogout.bind(this);
  }
  test = () => {
    const testing = this.props.notifications.results;
    let f = 0;
    for (let i = 0; i < testing.length; i++) {
      if (testing[i].readstatus === false) {
        f++
      }
    }
    if (f >= 1) {
      this.setState({ noti: false });
    } else {
      this.setState({ noti: true });
    }

  }
  componentDidMount() {
    this.test();
    this.setState({
      inbox: this.props.inbox.inbox,
      viewMessage: this.props.inbox.read,
    });
    
  }

  handleLogout() {
    this.props.logoutUser();
  }
  toggleNav = () => {
    this.setState({
      isNavOpen: !this.state.isNavOpen
    });
  }

  handleChange = (e) => {
    const target = e.target;
    const value = target.value;
    const name = target.name;
    this.setState({
      [name]: value

    });

  }
  handleSearch = (e) => {
    e.preventDefault();
    const bearer = 'Bearer ' + localStorage.getItem('token');
    var config = {
      headers: { 'Authorization': bearer }
    };
    const QUERY = this.state.search;
    fetch(baseUrl + `search/search?q=${QUERY}`, config)
      .then(data => data.json())
      .then(json => {
        return this.state.search === "" ? this.setState({ loading: false, results: [] }) : this.setState({ loading: false, results: json })
      })
      .catch(err => {
        this.setState({ loading: false, err: err })
      })

  }

  handleStyle = (e) => {
    e.preventDefault();
    this.setState({
      styled: this.state.styled === "none" ? "" : "none"
    })
    if (this.state.readTrue === false) {
      const bearer = 'Bearer ' + localStorage.getItem('token');
      var config = {
        method: "POST",
        headers: { 'Authorization': bearer }
      };
      const QUERY = JSON.parse(localStorage.getItem('id'))
      fetch(baseUrl + `notification/all/readtrue/${QUERY}`, config)
        .then(json => {
          this.setState({ readTrue: true })
          this.props.handleNotificationStatus();
        })
    }

  }
// getInboxread = async () => {
//   const id = await JSON.parse(localStorage.getItem('id'));
//   const inbox = !this.state.inbox ? [] : this.state.inbox
//   const message = inbox.some(i => i.talk.some(t => t.author !== id && t.seen === false))
//   console.log("chau", message);
//   return message;
// }
  render() {
    let messageAlert = this.state.viewMessage
    console.log(this.state);
    return (
      <React.Fragment>
        <Navbar className="container-fluid main-nav navbar-dark" expand="md">
          <div className="container">
            <NavbarToggler onClick={this.toggleNav} />
            <NavbarBrand className="" href="/">
              <img
                src={baseUrl + "images/0.png"}
                alt="Red Social"
                className="imagen-navbar-profile"
                height="30" width="41"
              />
            </NavbarBrand>
            <Collapse isOpen={this.state.isNavOpen} navbar  >
              <div className="segundoItemUno">
                <Form>
                  <FormGroup className="text-white search">
                    {/*<Label htmlFor="search">Search</Label>*/}
                    <Input name="search" id="search" className="btn bg-success input search" placeholder="Search"
                      onKeyUp={(e) => this.handleSearch(e)} onChange={this.handleChange} value={this.state.search} />
                    <SearchRender className="" search={this.state.results} />
                  </FormGroup>
                </Form>
              </div>
              <Nav navbar >
                <div className="itemGrupoDos ">
                  <NavLink className="nav-link" to="/userpage">
                    <img src={baseUrl + JSON.parse(localStorage.getItem('img'))}
                    alt="userimg" className="imagen-navbar-profile" />

                  </NavLink>
                </div>
                <div className="itemGrupoDos">
                  <NavItem>
                    <NavLink className="nav-link" to="/home">
                      <span className="fa fa-home fa-md" />
                    </NavLink>
                  </NavItem>
                </div>
                <div className="itemGrupoDos">
                  <NavItem>
                    <NavLink className="nav-link" to="/users/settings">
                      <span className="fa fa-cog fa-md" />
                    </NavLink>
                  </NavItem>
                </div>
                <div className="itemGrupoDos">
                  <NavItem>
                  {messageAlert ?
                      (<div>
                        <NavLink className="nav-link" to="/inbox">
                          <span className="fa fa-envelope fa-md" style={{ color: 'red' }}/>
                        </NavLink>
                      </div>)
                  :
                    (
                    <div>
                      <NavLink className="nav-link" to="/inbox">
                        <span className="fa fa-envelope fa-md" />
                      </NavLink>
                    </div>)
                    }
                    
                </NavItem>
                </div>
                <div className="itemGrupoDos">
                  <NavItem>
                    {this.state.noti === false ?
                      (<div>
                        <NavbarBrand className="nav-link" onClick={this.handleStyle}>
                          <span className="fa fa-bell fa-md fa-red fa-spin" style={{ color: 'red' }} />
                        </NavbarBrand>
                        <div>
                          <div className="row mt-4 notification" style={{ display: this.state.styled }}  >
                            <div className="col-12" >
                              <Notifications notifications={this.props.notifications} className="col-12"
                                friendRequestResponse={this.props.friendRequestResponse} />
                            </div>
                          </div>
                        </div>
                      </div>) :
                      (<div>
                        <NavbarBrand className="nav-link" onClick={this.handleStyle}>
                          <span className="fa fa-bell fa-md" />
                        </NavbarBrand>
                        <div style={{ display: this.state.styled }}  >
                        <div className="pik pick-notification"></div>
                          <div className="row mt-4 notification"  >
                            <div className="col-12" >                             
                              <Notifications notifications={this.props.notifications} className="col-12"
                                friendRequestResponse={this.props.friendRequestResponse} />
                            </div>
                            </div>
                        </div>
                      </div>
                      )}
                  </NavItem>

                </div>

                <div className="itemGrupoDos">
                  
                    {
                      !this.props.auth.isAuthenticated ? null : (
                        <UncontrolledDropdown nav inNavbar>
                          <DropdownToggle nav >
                            <span className="fas fa-align-justify fa-md" />
                          </DropdownToggle>
                          <DropdownMenu right className="dropmenu" >
                            <DropdownItem onClick={this.handleLogout}>
                              Logout
                                </DropdownItem>
                          </DropdownMenu>
                        </UncontrolledDropdown>
                      )
                    }
                </div>

              </Nav>
            </Collapse>
          </div>
        </Navbar>
      </React.Fragment >
    );
  }
}

export default Header;
