import React from 'react';
import { baseUrl } from '../shared/baseUrl';
import { Modal, ModalBody, ModalHeader, ModalFooter } from "reactstrap";
import styled from 'styled-components'
export const LikesModal = (props) => {
    const likesList = !props.likes ? [] : props.likes;
    const megustaamigos = likesList.map(amis => {
        return (
            <div key={amis._id}>
                <img className="" src={baseUrl + amis.image.filename} alt="img_profile"/>
                <h3>{amis.firsname} {amis.lastname}</h3>
            </div>
        )
    })
    return (
        <div>
            <Modal isOpen={props.isLikesModalOpen} toggle={props.toggle}>
                <ModalHeader id="likemodal" toggle={(e) => props.toggle(e)}><span>Me gusta</span></ModalHeader>
                <ModalBody>
                    <ModalLikes>
                        <div>
                            {megustaamigos}
                        </div>
                    </ModalLikes>
                </ModalBody>
                <ModalFooter></ModalFooter>
            </Modal>
        </div>
    )

}
const ModalLikes = styled.div`
display: grid;
text-overflow: ellipsis;
grid-gap: 10px;
grid-template-columns: 1fr 1fr 1fr;
grid-template-rows: 1fr;
grid-auto-rows: minmax(30%, 30%);
grid-auto-columns: minmax(30%, 30%);
align-items: center;

h6 {
    
    white-space: nowrap;
    align-items: center;
    margin-left: 15%;
}
img{
  margin: 15%;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  max-width: auto;
  min-whidth: auto;
  object-fit: cover;
}

@media (max-width: 1000px) {
  img{
  width: 40px;
  height: 40px;
  border-radius: 50%;
  max-width: auto;
  min-whidth: auto;
  object-fit: cover;
  }
}
@media (max-width: 767px) {
  img{
  width: 25px;
  height: 25px;
  border-radius: 50%;
  max-width: auto;
  min-whidth: auto;
  object-fit: cover;
  }
}
@media (max-width: 520px) {
  img{
  width: 20px;
  height: 20px;
  border-radius: 50%;
  max-width: auto;
  min-whidth: auto;
  object-fit: cover;
  }
}
@media (max-width: 343px) {
  img{
  width: 10px;
  height: 10px;
  border-radius: 50%;
  max-width: auto;
  min-whidth: auto;
  object-fit: cover;
  }
}
`