import React, { useLayoutEffect, useState } from "react";
import styled from 'styled-components'
import { RenderComments } from './CommentComponent';
import { LikesModal } from './LikesModal';
import { baseUrl } from '../shared/baseUrl';
import { withRouter, Link } from "react-router-dom";
import { Modal, ModalBody, Button } from 'reactstrap';
import { Control, LocalForm } from 'react-redux-form';

function useWindowSize() {
    const [size, setSize] = useState([0]);
    useLayoutEffect(() => {
      function updateSize() {
        setSize([window.innerWidth]);
      }
      window.addEventListener('resize', updateSize);
      updateSize();
      return () => window.removeEventListener('resize', updateSize);
    }, []);
    return size;
  }

class UsersComponent extends React.Component {
    constructor(props) {
        super(props);

        this.state = {

            loading: false,
            results: [],
            e: false,
            isNavOpen: false,
            isModalOpen: false,
            i: null,
            arraySource: [],
            comentarios: [],
            imgID: null,
            likes: [],
            isLikesModalOpen: false,
            size: null
        }
    }
    componentDidMount() {
        this.setState({loading: this.props.users.isLoading})
        const ID = this.props.match.params.idusers
                this.props.fetchDataUser(ID)
                .then(() => {
                    this.setState({results: [this.props.users.users]})
                    this.fetchData();
            })
    }

    handleFollow = (followerId) => {
        const followingId = this.props.user.user._id;
        const userData = followerId
        this.props.followFetch(followingId, userData)
            .then(() => {
                const ID = this.props.match.params.idusers
                this.props.fetchDataUser(ID)
            })
    }
    
    fetchData = () => {
        let sourced = this.state.results[0].imagesWall.map(img => `${baseUrl}${img.filename}`)
        this.setState({arraySource: sourced })
    }

    backForthModal = (e) => {
        e.preventDefault();
        let mode = e.target.id
        let currentlyUserId = this.state.results[0]._id;//id del usuario
        if (mode === "back") {
            let currentlyOne = this.state.i;
            let back = this.state.arraySource.indexOf(currentlyOne) - 1
            let elementBack = this.state.arraySource[back]
            this.setState({
                i: elementBack
            })
            this.fetchPhotoId(e, currentlyUserId, elementBack);
        } else {
            let currentlyOne = this.state.i;
            let forth = this.state.arraySource.indexOf(currentlyOne) + 1
            let elementForth = this.state.arraySource[forth]
            this.setState({
                i: elementForth
            })
           

            this.fetchPhotoId(e, currentlyUserId, elementForth);
        }
    }

    toggleModal = (e, valueId) => {
        e.preventDefault();
        var mgID = e.target.id;//id imagen
        if (valueId === "wall") {
            this.setState({
                isModalOpen: !this.state.isModalOpen,
                i: e.target.src,//url imagen
                imgID: mgID
            });
            this.fetchComments(mgID)
            this.fetchUsersLikes(e);
        } else if (valueId === "likemodal") {
            this.setState({
                isLikesModalOpen: !this.state.isLikesModalOpen,
            });
            this.fetchUsersLikes(e)
        } else {
            this.setState({
                isModalOpen: false,
                isTrashModalOpen: false,
                isLikesModalOpen: false
            });
        }
    }

    fetchPhotoId = (e, ID, element) => {
        let bodyPath = element.split(baseUrl)[1]
        var config = {
            foto: bodyPath
        };
        return fetch(baseUrl + `users/returnid/${ID}`, {
            method: "POST",
            body: JSON.stringify(config),
            headers: {
                "Content-Type": "application/json"
            }
        })
            .then(data => data.json())
            .then(response => {
                let result = response.result
                this.setState({ imgID: result })
                return result;
            })
            .then((result) => {
                this.fetchComments(result)
                this.fetchUsersLikes(e)
            })
            .catch(err => {
                this.setState({ loading: false, e: err })
            })
    }
    fetchComments = (ID) => {
        return fetch(baseUrl + `comments/get-comments-image/${ID}`)
            .then(data => data.json())
            .then(json => {
                this.setState({
                    comentarios: json
                })
            })
            .catch(err => {
                this.setState({ e: err })
            })
    }
 
    fetchUsersLikes = async (event) => {
        event.preventDefault();
        let userId = await this.state.results[0]._id;
        let imgId = await this.state.imgID;
        await this.props.fetchLikes(userId, imgId)
            .then((likeResult) => {
                this.setState({
                    likes: this.props.likes
                })
            })
    }

    render() {
        console.log("state",this.state)
        console.log("state",this.props.users.isLoading)
        
        let filtrado = JSON.parse(localStorage.getItem("id"));
        
        let currentlyPictureId = !this.state.likes ? [] : this.state.likes.filter(user => user._id === filtrado)[0];

        return (
            <div className="container-fluid ">

                <Modal isOpen={this.state.isModalOpen} toggle={(e) => this.toggleModal(e)} size="lg" >
                    <div style={this.state.isModalOpen && this.state.arraySource.indexOf(this.state.i) !== 0 ?
                        { color: "black", fontSize: "40px", position: "absolute", left: "-50px", top: "40%", zIndex: "1" } : { display: "none" }}>
                        <span className="fas fa-angle-left" id="back" onClick={(event) => this.backForthModal(event)}></span>
                    </div>
                    <div style={this.state.isModalOpen && this.state.arraySource.indexOf(this.state.i) !== this.state.arraySource.length - 1 ?
                        { color: "", fontSize: "40px", position: "absolute", right: "-50px", top: "40%", zIndex: "1" } : { display: "none" }}>
                        <span className="fas fa-angle-right" id="forth" onClick={(event) => this.backForthModal(event)}></span>
                    </div>
                    <LikesModal isLikesModalOpen={this.state.isLikesModalOpen} toggle={this.toggleModal} likes={this.state.likes} />
                    <LocalForm onSubmit={(values) => this.handleSubmit(values)}>
                        <ModalBody toggle={(e) => this.toggleModal(e)} className="modalWithoutPadding">
                            <ModalGrid>
                                <div className="modalgroup">
                                    <div className="c">
                                        <img className="resp" src={this.state.i} alt="wall grid" />
                                    </div>
                                    <div className="modalComments">
                                        <div className="comm">
                                            <RenderComments comentarios={this.state.comentarios} state={this.state} deleteComments={this.deleteComments} />
                                        </div>
                                        <div>{
                                            this.state.likes.length > 0
                                                ?
                                                <h6>Le gusta esta foto a <strong onClick={(event) => this.toggleModal(event, "likemodal")} >{this.state.likes.length} usuarios</strong></h6>
                                                :
                                                <h6>Se el primero en marcar me gusta</h6>
                                        }</div>
                                        <div className="foot">
                                            <div className="texto">
                                                <Control.textarea model=".comment" id="comment" />
                                            </div>
                                            <div className="footButtons">
                                                <div className="ml-2">
                                                    <Button onClick={(event) => this.handleLike(event)}>
                                                        {
                                                            !currentlyPictureId
                                                                ?
                                                                <span className="fa fa-heart"></span>
                                                                :
                                                                <span className="fa fa-heart" style={{ color: 'red' }}></span>

                                                        }
                                                    </Button>
                                                </div>
                                                <div className="buton btn-sm" >
                                                    <Button type="submit" className="btn-sm">Enviar</Button>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </ModalGrid>

                        </ModalBody>

                    </LocalForm>
                </Modal>
                <div>
                    <Render loading={this.state.loading} data={this.state.results} handleFollow={this.handleFollow}
                        followers={this.props.followers} toogle={this.toggleModal} />
                </div>
            </div>
        )



    }
}

const Render = (props) => {
    const [width] = useWindowSize();
    let screen = width >= 600 ? true : false
    const Data = props.data;
    console.log("screen", document.width);
    console.log("screen", screen);

    const USUARIOS = Data.map(user => {
        let loading = props.loading === true ? true : false
        const foll = user.followers.some(foll => foll.id._id === JSON.parse(localStorage.getItem('id')) ? true : false);
        const noti = user.notifications.some(noti => noti.followingId === JSON.parse(localStorage.getItem('id')) ? true : false);
        return (
            <div key={user._id}>
                <ProfileImage className="">
                    <div className="image-content">
                        <img
                            src={baseUrl + user.image.filename} alt="Profile One"
                            className="img-thumbnail"
                        />
                     </div>
                </ProfileImage>
                {
                    loading === true ?
                        <div className="">
                            <Button type="button" className="btn-success col-2 offset-5 mt-3 ">
                                <span className="fas fa-spinner fa-spin"></span>
                            </Button>
                        </div> :
                        noti === true ?
                            <div className="">
                                <Button type="button" className="btn-success col-2 offset-5 mt-3 ">
                                    <span className="fas fa-hourglass-half fa-spin mr-2"></span>Wait Response
                                </Button>
                            </div>

                            : foll === false ?
                                <div>
                                    <Button type="button" className="btn-success col-2 offset-5 mt-3 fas fa-user-plus" onClick={() => props.handleFollow(user._id)} />
                                </div> :

                                <div >
                                    <Button type="button" className="btn-success col-2 offset-5 mt-3">Siguiendo</Button>
                                </div>


                }
                    <div className="user-info">
                    <div className="user-info-description">
                        <h5 className="">{user.firstname} {user.lastname}</h5>
                        <h5 className="">{`"${user.phrase}"`}</h5>
                        <h5 className="">{user.followers.length} Seguidores - {user.following.length} Seguidos</h5>
                    </div>
                    </div>
                {
                    user.imagesWall && screen ?
                    
                        <ImageGrids className="image-grid">
                            {user.imagesWall.map((img) => {

                                return (

                                    <div className="image-content" key={img._id}>
                                        <img className="img-thumbnail img-grid" src={baseUrl + img.filename} alt={img._id} id={img._id} onClick={(e) => props.toogle(e, "wall")} />
                                    </div>

                                )
                            })}
                        </ImageGrids>
                        :
                        <ImageGrids className="image-grid">
                            {user.imagesWall.map((img) => {

                                return (
                                    
                                        <div className="image-content" key={img._id}>
                                            <Link to={`/view/imagenwall/${img._id}`}><img className="img-thumbnail img-grid" src={baseUrl + img.filename} alt={img._id} id={img._id} /></Link>
                                        </div>
                                )
                            })}
                        </ImageGrids>
                }
            </div>
        )
    })
    return (

        <div  >{USUARIOS}</div>

    )
}
const ProfileImage = styled.div`
display: flex;
flex-direction: row;
justify-content: center;
.image-content {
  width: 21vw;
  height: 21vw;
}


img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
}
`

const ImageGrids = styled.div`

    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    box-sizing: border-box;
    margin: auto 5%;

.image-content {
    width: 33%;
    height: 26vw;
  }
  
  
  .img-grid{
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

`
const ModalGrid = styled.div`
display: flex;
flex-direction: row;
justify-content: space-between;

div.modalgroup {
    display: flex;
flex-direction: row;
justify-content: space-between;
}
div.modalComments {
    display: flex;
flex-direction: column;
justify-content: space-between;

}
div.foot {
    display: flex;
flex-direction: row;
    margin-left: 5%;
}
div.footButtons {
    display: flex;
flex-direction: column;
}
div.comm {
    height: 460px;
    display: block;
    overflow-y: scroll;
  scroll-behavior: smooth;
}
div.c {
    width: 450px;
    height: 550px;
}
img.resp {
    
    width: 100%;
    height: 100%;
    max-width: auto;
    min-whidth: auto;
    object-fit: contain;
  }
  img.comment{
    margin-left: 5%;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    max-width: auto;
    min-whidth: auto;
    object-fit: cover;
  }
  @media (max-width: 991px) {
    div.modalComments {
      display: none;
    }
  }
`
export default withRouter(UsersComponent);
