//Dependencis
import React from 'react';
import { Media, Button } from 'reactstrap';
import styled from 'styled-components'
import { baseUrl } from '../shared/baseUrl';
import { withRouter } from "react-router-dom";


class Notifications extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            data: []
        }
    }
    componentDidMount() {
        const datos = this.props.notifications
        this.setState({ data: datos })
    }
    handleRequestFriendship = (action, followerId, notiId) => {
        const dataNotification = {
            action: action,
            followerId: followerId,
            notiId: notiId
        }
        this.props.friendRequestResponse(dataNotification)
            .then(() => {
                this.props.history.push("/");
                window.location.reload();
            })
    }

    render() {
        let notif = this.state.data ? this.state.data.results : []
        const NOTIFICACIONES = !notif ? [] : notif.map((n) => {
            return (
                <div key={n._id} >
                    <Searching className="mt-1 mb-1 " >
                        <div className="row ">
                            <Media tag="li" className="col-12 align-notification">
                                <Media left className="col-3 mx-0 px-0">
                                    <Media className="imagen-navbar-profile" object src={baseUrl + n.followingId.image.filename} ></Media>
                                </Media>
                                <div className="col-9" >
                                    <div className="row">
                                        <div className="col-6" >
                                            <Media body className="col-8">
                                                <Media heading><span>{n.message}</span></Media>
                                            </Media>
                                        </div>
                                        <div className="col-3" >
                                            <Button className="fas fa-thumbs-up" style={{ color: 'rgb(62, 247, 62)' }} onClick={() => this.handleRequestFriendship(true, n.followingId._id, n._id)} />
                                        </div>
                                        <div className="col-3" >
                                            <Button className="fas fa-thumbs-down" style={{ color: 'rgb(245, 53, 19)' }} onClick={() => this.handleRequestFriendship(false, n.followingId._id, n._id)} />
                                        </div>
                                    </div>
                                </div>
                            </Media>
                        </div>
                        </Searching>
                    </div>
                
            );
        })
        return (
            <Searching>
                <div className="container position-absolute ">
                    <div className="row ">
                        <Media list className="p-0 ">
                            {NOTIFICACIONES}
                        </Media>
                    </div>
                </div>
            </Searching>
        )
    }
}


const Searching = styled.div`
background-color: rgb(91, 179, 94);
position: relative;
padding:0px;
width: 100%;
z-index: 1;
color: white;
font-size: 1em;
margin: 0px;
align-items:center;
.align-notification{
    display: flex;
    align-items:center;
}
img{
    margin: 15%;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    max-width: auto;
    min-whidth: auto;
    object-fit: cover;
  }
  span {
      font-size: 1rem;
  }
`
export default withRouter(Notifications);
