import React from 'react';
import { baseUrl } from '../../shared/baseUrl';
import styled from 'styled-components';
import { Input, FormGroup, Form } from 'reactstrap'

export default class Mensajes extends React.Component {
    
    chatContainer = React.createRef();
    componentDidUpdate(){
        this.scrollToMyRef()

    }
    scrollToMyRef = () => {
        let scroll = this.chatContainer.current.scrollHeight - this.chatContainer.current.clientHeight;
        this.chatContainer.current.scrollTo(0, scroll);
      };
    render() {
        const inboxStatus = !this.props.chatTitle ? [] :this.props.chatTitle
        const inboxMessages = this.props.chatUsers !== [] ? this.props.chatUsers : []
        const inboxMessagesWindow = inboxMessages[0]
        console.log("inboxStatus", inboxStatus);
        const INBOX = inboxStatus.map((i,index) => {
            // const IMAGINA = i.id._id === JSON.parse(localStorage.getItem('id')) ? i.id.image.filename : JSON.parse(localStorage.getItem('img'))
            let IMAGINA = i.id.image.filename;
            return (
                <div key={index}>
                        <ImageTitle>
                            <div>
                                <img src={baseUrl + IMAGINA} alt="chatimgstatus" />
                            </div>

                            <div className="hide-name">
                                <h6>{i.id.firstname} {i.id.lastname}</h6>
                            </div>
                        </ImageTitle>
                </div>
            )
        })
        let MESSAGES = !inboxMessagesWindow ? [] : inboxMessagesWindow.talk.map(l => {
            let styling = l.author === JSON.parse(localStorage.getItem("id")) ? this.props.chatStyle.own : this.props.chatStyle.they
            return (

                <div key={l._id} style={styling.margin}>
                    <div style={styling}>
                        <h6>{l.content}</h6>
                    </div>
                </div>

            )
        })

        return (
            <div  className="container-fluid inbox" >
                <div className="row m-3 align-items-center">
                    <h5>Mensajes</h5>
                    <div className="justify-content-end">{INBOX}</div>
                </div>
                <div ref={this.chatContainer} className="inboxes">
                    {!this.props.chatTitle[0] ?
                        <div>
                            <h1>Estado de la Conversacion</h1>
                            <h4>Elije una conversacion o un Amigo</h4>
                        </div>
                        :
                        <div className="message-display"> 
                            <div className="">
                                {MESSAGES}
                            </div>
                            <div>
                                <Form onSubmit={(e) => this.props.handleSubmitInbox(e)}>
                                    <FormGroup className="in-inbox">
                                        <div className="chat-field">
                                                <div className="chat-input">
                                                    <Input
                                                        type="text"
                                                        id="message"
                                                        name="message"
                                                        className="h-100"
                                                    />
                                                </div>
                                                <button type="submit" className="fas fa-paper-plane button-chat" />
                                        </div>
                                    </FormGroup>
                                </Form>
                            </div>
                        </div>
                }
                </div>
            </div>
    )
 }
}
const ImageTitle = styled.div`
display:flex;
align-items: center;
justify-content: center;
margin-left: 15px;
img{
    margin-right: 10px;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    max-width: auto;
    min-whidth: auto;
    object-fit: cover;
  }
`