import React from 'react';
import { baseUrl } from '../../shared/baseUrl';
import styled from 'styled-components'
export default class Chats extends React.Component {
    render() {
        let usuario = JSON.parse(localStorage.getItem('id'));
        const talksiStatusMap = this.props.talks ? this.props.talks : []
        console.log("inboxwww", talksiStatusMap);
        const TALKS = talksiStatusMap.map(t => {
            const imagen = t.members.userOne._id === JSON.parse(localStorage.getItem('id')) ? t.members.userTwo.image.filename : JSON.parse(localStorage.getItem('img'))
            const name = t.members.userOne._id === JSON.parse(localStorage.getItem('id')) ? `${t.members.userTwo.firstname} ${t.members.userTwo.lastname}` : `${t.members.userOne.firstname} ${t.members.userOne.lastname}`
            let seenIt = t.talk.some(element => element.author !== usuario && !element.seen)
            const inboxdata = {
                id: t._id,
                room: t.room
            }
            console.log("x", inboxdata);
            return (
                <div key={t._id}>
                    { seenIt ?
                    <div className="chats cursor" onClick={(e) => this.props.inboxTalksHandler(e, inboxdata)}>
                        <ImageGrida>
                            <div>
                                <img src={baseUrl + imagen} alt="chatimgstatus" />
                            </div>

                            <div >
                                <h6 id={t._id} >{name}</h6>
                            </div>
                        </ImageGrida>
                    </div>
                    :
                    <div className="cursor" onClick={(e) => this.props.inboxTalksHandler(e, inboxdata)}>
                    <ImageGrida>
                        <div>
                            <img src={baseUrl + imagen} alt="chatimgstatus" />
                        </div>

                        <div >
                            <h6 id={t._id}>{name}</h6>
                        </div>
                    </ImageGrida>
                </div>
        }
                </div>
            )
        })
        return (
            <div className="container-fluid inbox">
                <div className="row m-3">
                    <h5>Chats</h5>
                </div>
                <div className="row inboxes">
                    {TALKS}
                </div>

            </div>
        )
    }
}
const ImageGrida = styled.div`
display: grid;
grid-gap: 10px;
grid-template-columns: 1fr 1fr 1fr;
grid-template-rows: 1fr;
grid-auto-rows: minmax(30%, 30%);
grid-auto-columns: minmax(30%, 30%);
align-items: center;
h6 {
    width: 100%;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    align-items: center;
    margin-left: 15%;
}
img{
  margin: 15%;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  max-width: auto;
  min-whidth: auto;
  object-fit: cover;
}

@media (max-width: 1000px) {
  img{
  width: 40px;
  height: 40px;
  border-radius: 50%;
  max-width: auto;
  min-whidth: auto;
  object-fit: cover;
  }
}
@media (max-width: 767px) {
  img{
  width: 25px;
  height: 25px;
  border-radius: 50%;
  max-width: auto;
  min-whidth: auto;
  object-fit: cover;
  }
}
@media (max-width: 520px) {
  img{
  width: 20px;
  height: 20px;
  border-radius: 50%;
  max-width: auto;
  min-whidth: auto;
  object-fit: cover;
  }
}
@media (max-width: 343px) {
  img{
  width: 10px;
  height: 10px;
  border-radius: 50%;
  max-width: auto;
  min-whidth: auto;
  object-fit: cover;
  }
}
`