import React from 'react';
import classnames from 'classnames';
import styled from 'styled-components';
import { Loading } from "./LoadingComponent";
import { TabContent, TabPane, Nav, NavItem, NavLink, Button } from 'reactstrap';
import { Control, LocalForm } from 'react-redux-form';
import { Link } from 'react-router-dom';
import { baseUrl } from '../shared/baseUrl';

class Start extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            content: [],
            contentLoading: true,
            following: [],
            followers: [],
            followingLoading: true,
            usuario: JSON.parse(localStorage.getItem("creds")).username

        }
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    componentDidMount() {
        this.setState({
            content: this.props.start.start !== null ? [this.props.start.start] : [],
            contentLoading: !this.props.start ? true : this.props.start.isLoading,
            following: !this.props.following ? [] : this.props.following.following,
            followers: !this.props.followers ? [] : this.props.followers.followers,
            followingLoading: !this.props.following ? true : this.props.following.isLoading
        })
        this.props.socketConection.on("chatNotification", data => {
            console.log("llego el socket main", data);
            this.props.inboxFetch();
            // let url = this.props.match.url
            // console.log("uuurrrlll", url);
            // if(url === 'ggg'){
            //   console.log("uuurrrlll", url);
            //   this.props.inboxFetch();
            // }
        })
        
    }


    handleSubmit(values, event) {
        event.preventDefault();
        const commenta = {
            comment: values.comment,
            author: localStorage.getItem("id"),
            image: event.target.id
        }
        this.props.commentsPost(commenta)
            .then((rest) => {
                this.props.fetchStart()
            })

    }
    handleLike = async (event, imgI) => {
        event.preventDefault();
        let img = await imgI;
        var usersData = {
            id: JSON.parse(localStorage.getItem('id')),
            liked: img
        }
        this.props.postLike(img, usersData)
            .then(async () => {
                await this.props.fetchStart()
            })
    }

    render() {

        return (
            <div className="container-fluid">
                <div className="row">
                <div className="col-12 col-md-8 col-lg-6 offset-md-2 ">
                    <ImgWall start={this.state.content} handleLike={this.handleLike}
                        handleSubmit={this.handleSubmit} Loading={this.state.contentLoading} 
                        usuario={this.state.usuario}/>
                </div>
               
                    <Amigos following={this.state.following} followers={this.state.followers}
                    LoadState={this.state.followingLoading} usuario={this.state.usuario}/>
               
                </div>
            </div>
        )
    }

}

const ImgWall = (props) => {
    let load = props.Loading;
    if (load === true) {
        return (
            <div >

                <Loading />

            </div>
        )
    }
    else {
        const aaa = props.start === [] ? [] : props.start[0];

        const WALL = aaa.map((first) => {
            let filtrado = JSON.parse(localStorage.getItem("id"));
            let currentlyPictureId = first.imageId.likes.some(user => user === filtrado);
            return (
                <div className="row" key={first._id}>
                    <div className=" backi">
                        <div className=" imagenesWallHeadFrame">
                            <Link to={`/users/profile/${props.usuario}/${first.userId.username}`} >
                                <div className="frameOne">
                                    <img className="imagenesWallHead" src={baseUrl + first.userId.image.filename} alt={first.userId.image._id} />
                                    <h6 className="ml-3">{first.userId.firstname} {first.userId.lastname}</h6>
                                </div>
                            </Link>
                            <div className="frameTwo">
                                <Button className="corazon" onClick={(event) => props.handleLike(event, first.imageId._id)}>
                                    {
                                        currentlyPictureId ?
                                        <span className="fa fa-heart" style={{ color: 'red' }}></span>
                                            :
                                            <span className="fa fa-heart" style={{ color: 'grey' }}></span>
                                    }
                                </Button>
                            </div>
                        </div>

                        <div className=" img-thumbnail imagenesWallFrame">
                            <img className="imagenesWall" src={baseUrl + first.imageId.filename} alt={first.imageId._id} />
                        </div>
                        <div className="commentsAndForm">
                        <div className="commentsWallHeadFrame">
                            {first.commento[0] !== undefined ?
                                <div className="mt-2">
                                    <h6 className="">Hay {first.commento[0].length} comentarios</h6>
                                </div>
                                :
                                null}
                            {first.commento[0] !== undefined && first.commento[0][0] === undefined ?
                                (
                                    <div className="commentsContent">
                                        <Link to={`/users/profile/${props.usuario}/${first.commento[0].author.username}`} >
                                            <h6 className="author" >{first.commento[0].author.username}</h6>
                                        </Link>
                                        <h6 className="comments" >{first.commento[0].comment}</h6>
                                    </div>

                                )
                                : first.commento[0] !== undefined ?
                                    (
                                        <div>
                                            <div className="commentsContent">
                                                <Link to={`/users/profile/${props.usuario}/${first.commento[0][0].author.username}`}>
                                                    <h6 className="author" >{first.commento[0][0].author.username}</h6>
                                                </Link>
                                                <h6 className="comments" >{first.commento[0][0].comment}</h6>
                                            </div>
                                            <div className="commentsContent">
                                                <Link to={`/users/profile/${props.usuario}/${first.commento[0][1].author.username}`} >
                                                    <h6 className="author" >{first.commento[0][1].author.username}</h6>
                                                </Link>
                                                <h6 className="comments" >{first.commento[0][1].comment}</h6>
                                            </div>
                                        </div>

                                    )
                                    :
                                    (<div className="mt-2 ml-0">
                                        <h6 className="" >No hay comentarios todavia</h6>
                                    </div>
                                    )}
                        </div>
                            <div className="">
                                <LocalForm onSubmit={(values, event) => props.handleSubmit(values, event)} id={first.imageId._id}>
                                    <div className="inputsWallFrame">
                                        <Control.textarea model=".comment" id="comment" placeholder="Comenta aqui..." className="inputsWall" />

                                        <Button type="submit" className="buttonComment">Enviar</Button>
                                    </div>
                                </LocalForm>
                            </div>
                        </div>
                    </div>
                </div>
            )
        })
        return (
            <div>
                {WALL}
            </div>
        )
    }
}

class Amigos extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            activeTab: '1'
        }
    }

    toggle = tab => {
        if (this.state.activeTab !== tab) this.setState({ activeTab: tab })
    }

    render() {
        let loadS = this.props.LoadState
        if (loadS === true) {
            return (
                <div >

                    <Loading />

                </div>
            )
        }
        else {
            const mapeoFollowing = this.props.following ? this.props.following : [];
            const mapeoFollowers = this.props.followers ? this.props.followers : [];

            //const followingFilter = mapeo.filter(friend => friend._id !== userOne || friend._id !== userTwo)
            const FOLLOW = mapeoFollowing.map(f => {
                return (
                    <div key={f._id}>
                        <Link to={`/users/profile/${this.props.usuario}/${f.id.username}`}>
                            <div className="" >
                                <ImageGrida>
                                    <div>
                                        <img src={baseUrl + f.id.image.filename} alt="chatimgstatus" />
                                    </div>

                                    <div >
                                        <h6 id={f.id._id}>{`${f.id.firstname} ${f.id.lastname}`}</h6>
                                    </div>
                                </ImageGrida>
                            </div>
                        </Link>
                    </div>

                )

            })
            const FOLLOWERS = mapeoFollowers.map(f => {
                return (
                    <div key={f._id}>
                        <Link to={`/users/profile/${this.props.usuario}/${f.id.username}`} >
                            <div className="" >
                                <ImageGrida>
                                    <div>
                                        <img src={baseUrl + f.id.image.filename} alt="chatimgstatus" />
                                    </div>

                                    <div >
                                        <h6 id={f.id._id} >{`${f.id.firstname} ${f.id.lastname}`}</h6>
                                    </div>
                                </ImageGrida>
                            </div>
                        </Link>
                    </div>

                )

            })

            return (
                <div className="d-none d-lg-block  col-3 sidenav" >
                    <Nav tabs className="row h-5">
                        <NavItem className="col-6">
                            <NavLink
                                className={classnames({ active: this.state.activeTab === '1' })}
                                onClick={() => { this.toggle('1'); }}>
                                Siguiendo
                            </NavLink>
                        </NavItem>
                        <NavItem className="col-6">
                            <NavLink
                                className={classnames({ active: this.state.activeTab === '2' })}
                                onClick={() => { this.toggle('2'); }}>
                                Seguidores
                            </NavLink>
                        </NavItem>
                    </Nav>
                    <TabContent activeTab={this.state.activeTab}>
                        <TabPane tabId="1">
                            <div className="row scroll-aside" style={{ backgroundColor: "aliceblue", borderRadius: "5%" }}>
                                <h6 className="col-md-12">{FOLLOW}</h6>
                            </div>
                        </TabPane>
                        <TabPane tabId="2">
                            <div className="row scroll-aside" style={{ backgroundColor: "aliceblue", borderRadius: "5%" }}>
                                <h6 className="col-md-12">{FOLLOWERS}</h6>
                            </div>
                        </TabPane>
                    </TabContent>
                </div>
            )
        }
    }
}

export default Start;

const ImageGrida = styled.div`
display: grid;
grid-gap: 10px;
grid-template-columns: 1fr 1fr 1fr;
grid-template-rows: 1fr;
grid-auto-rows: minmax(30%, 30%);
grid-auto-columns: minmax(30%, 30%);
align-items: center;
border-radius: 20%;

h6 {
    width: 100%;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    align-items: center;
    margin-left: 30px;
}
img{
  margin-top: 10px;
  margin-left: 10px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  max-width: auto;
  min-whidth: auto;
  object-fit: cover;
}

@media (max-width: 1000px) {
  img{
  width: 40px;
  height: 40px;
  border-radius: 50%;
  max-width: auto;
  min-whidth: auto;
  object-fit: cover;
  }
}
@media (max-width: 767px) {
  img{
  width: 25px;
  height: 25px;
  border-radius: 50%;
  max-width: auto;
  min-whidth: auto;
  object-fit: cover;
  }
}
@media (max-width: 520px) {
  img{
  width: 20px;
  height: 20px;
  border-radius: 50%;
  max-width: auto;
  min-whidth: auto;
  object-fit: cover;
  }
}
@media (max-width: 343px) {
  img{
  width: 10px;
  height: 10px;
  border-radius: 50%;
  max-width: auto;
  min-whidth: auto;
  object-fit: cover;
  }
}
`