import React from 'react';
import styled from 'styled-components'
import { baseUrl } from '../shared/baseUrl';
import { RenderComments } from './CommentComponent';
import { withRouter} from "react-router-dom";
import {
    Button
  } from "reactstrap";
  
import { Control, LocalForm } from 'react-redux-form';

class ImagenComponent extends React.Component {
    constructor() {
        super();
        this.state = {
            data: null,
            likes: null,
            comentarios: null
        }
    }
    componentDidMount() {
        const ID = this.props.match.params.idimg;
        const bearer = "Bearer " + localStorage.getItem("token");
        return fetch(`${baseUrl}imagen/view/imagenwall/${ID}`, {
            method: "GET",
            headers: {
                Authorization: bearer
            }
        })
            .then(resp => resp.json())
            .then(img => {
                this.setState({
                    data: img,
                    likes: img.likes
                })
            })
            .then(() => {
                return fetch(baseUrl + `comments/get-comments-image/${ID}`)
                    .then(data => data.json())
                    .then(json => {
                        this.setState({
                            comentarios: json
                        })
                    })
            })
            .catch((err) => {
                console.log(err);

            });
    }
    handleSubmit(values, event) {
      event.preventDefault();
      const commenta = {
          comment: values.comment,
          author: localStorage.getItem("id"),
          image: event.target.id
      }
      const commentState = {
        comment: values.comment,
        author: this.props.user.user,
        image: this.state.imgID
    }
      console.log("eee", commenta);
      this.props.commentsPost(commenta)
      .then(() => {
        this.setState({
            comentarios: this.state.comentarios.concat(commentState)
        })

    })
  }
  handleLike = async (event, imgI) => {
      event.preventDefault();
      let img = await imgI;
      var usersData = {
          id: JSON.parse(localStorage.getItem('id')),
          liked: img
      }
      this.props.postLike(img, usersData)
          .then((rest) => {
            let data = this.state.likes
            let likeResponse = !data ? null : data.some(user => user._id === usersData.id)
              if(likeResponse){
                let o = data.findIndex(i => i._id === usersData.id)
                let update = data.splice(o, 1);
                console.log(update);
                this.setState({likes: data})
              } else {
                console.log("spsps", usersData);
                let update = data.push({_id: usersData.id})
                console.log(update);
                this.setState({likes: data})
              }
            })
          }
  deleteComments = (ID) => {
    const bearer = 'Bearer ' + localStorage.getItem('token');
    return fetch(baseUrl + `comments/get-comments-image/${ID}`, {
      method: "DELETE",
      headers: {
        'Authorization': bearer,
        "Content-Type": "application/json"
      }
    })
      .then(data => data.json())
      .then(json => {
        this.setState({
          comentarios: this.state.comentarios.filter(item => item._id !== json._id)
        });
      })
      .catch(err => {
        this.setState({ e: err })
      })
  }
  deletePhotograph = (imgId) => {
    // var config = {
    //     userid: JSON.parse(localStorage.getItem("id")),
    //     imageid: imgId
    //   }
    //   console.log("fff", config);
    this.setState({ loading: true })
    const bearer = 'Bearer ' + localStorage.getItem('token');
    var config = {
      userid: JSON.parse(localStorage.getItem("id")),
        imageid: imgId
    }
    return fetch(baseUrl + `imagen/removeimage`, {
      method: "POST",
      body: JSON.stringify(config),
      headers: {
        'Authorization': bearer,
        "Content-Type": "application/json"
      }
    })
      .then(data => data.json())
      .then(json => {
        this.props.history.push("/userpage")
      })
      .catch(err => {
        this.setState({ loading: false, e: err })
      })
  }
    render() {
      console.log("st", this.state);
        const imagenes = !this.state.data ? [] : this.state.data;
        const likes = !this.state.likes ? [] : this.state.likes;
        const commen = !this.state.comentarios ? [] : this.state.comentarios;
        let filtrado = JSON.parse(localStorage.getItem("id"));
        let currentlyPictureId = !likes ? []:likes.some(user => user._id === filtrado)
        let trashButton = imagenes.userData ? imagenes.userData._id === filtrado : null
        console.log("st", trashButton);
        return (
                <div>
                <Modalidad>
                  <div className="modalgroup">
                    <div className="c">
                      <div className="trashIcon">
                        {trashButton ?
                          <Button id="trashId"><span className=" fa fa-trash-alt " onClick={()=> this.deletePhotograph(imagenes._id)}></span></Button>
                          : null
                          }
                      </div>
                      <img src={`${baseUrl}${imagenes.filename}`} alt={imagenes._id} className="resp"/>

                    </div>
                    <div className="modalComments">
                      <div className="comm">
                        {/* <RenderComments comentarios={commentarios} deleteComments={deleteComments} /> */}
                        <RenderComments comentarios={commen} deleteComments={this.deleteComments}/>

                      </div>
                      <div className="comm-likes">{
                        // likes.length > 0
                        true
                          ?
                        //   <h6>Le gusta esta foto a <strong id="likemodal" onClick={(event) => toggleModal(event)} >{likes.length} usuarios</strong></h6>
                        <h6>Le gusta esta foto a <strong id="likemodal">usuarios</strong></h6>

                          :
                          <h6>Se el primero en marcar me gusta</h6>
                      }</div>
                      
                      <LocalForm onSubmit={(values, event) => this.handleSubmit(values, event)} id={imagenes._id}>
                      <div className="foot">
                          <div className="texto">
                              <Control.textarea model=".comment" id="comment" className="textarea"/>
                          </div>
                          <div className="footButtons">
                              <div>
                                  <Button onClick={(event) => this.handleLike(event, imagenes._id)}>
                                      {
                                          !currentlyPictureId
                                              ?
                                              <span className="fa fa-heart"></span>
                                              :
                                              <span className="fa fa-heart" style={{ color: 'red' }}></span>

                                      }
                                  </Button>
                              </div>
                              <div className="" >
                                  <Button type="submit"><span className="fas fa-paper-plane"></span></Button>
                              </div>
                          </div>
                          </div>
                          </LocalForm>
   



                    </div>
                  </div>
                </Modalidad>
            </div>
        )
    }
}

const Modalidad = styled.div`
display: flex;
flex-direction: row;
justify-content: center;

div.modalgroup {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height:85vh;
  width:70%;
}
div.modalComments {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 32%;
  margin-left: 10px;
}
div.c {
  width: 63%;
}
div.foot {
  display: flex;
  flex-direction: row; 
}
textarea {
  width:100%;
  height: 100%;
}
div.footButtons {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.foot Button {
  // border-style: hidden;
  color: black;
  background-color: transparent;
}
div.comm {
  height: 80%;
  display: block;
  overflow-y: scroll;
  scroll-behavior: smooth;
}

img.resp {

  width: 100%;
  height: 100%;
  max-width: auto;
  min-whidth: auto;
  object-fit: contain;
}
img.comment{
  margin-left: 5%;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  max-width: auto;
  min-whidth: auto;
  object-fit: cover;
}
@media (max-width: 747px) {
  div.modalgroup {
    display: inline-block;
    width: 100%;
    margin-top: -21px;
  }
  div.modalComments {
    display: flex;
    justify-content: stretch;
    flex-direction: column-reverse;
    width: 100%;
    margin: 0px;
  }
  div.comm-likes  {
    display: none;
  }
  div.comm  {
    margin-top: 15px;
    display: flex;
    justify-content: start
    max-height: 300px;
  }
  div.foot{
    display: flex;
    justify-content:stretch;
  }
  .texto {
    width:100%;
  }
  div.c {
    display:inline-block
    width: 100%;
  }
  img.resp {
    width: 100%;
    object-fit: fit;
  }
}
}
`
export default withRouter(ImagenComponent);