import React from 'react';
import Chats from './Inbox/ChatsComponent';
import Mensajes from './Inbox/MensajesComponent';
import AmigosComponent from './Inbox/AmigosComponent';
// import { baseUrl } from '../shared/baseUrl';
// import io from 'socket.io-client';

class RenderInbox extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            room: "",
            followers: [],
            talks: [],
            chatTitle: [],
            chatUsers: [],
            id: "",
            loading: false,
            idTalk: "",
            style: {
                own: {
                    display: "flex",
                    justifyContent: "flex-end",
                    backgroundColor: "green",
                    borderRadius: "18.2px",
                    width: "auto",
                    padding: "5px 10px",
                    maxWidth: "64%",
                    color: "white",
                    margin: {
                        display: "flex",
                        justifyContent: "flex-end",
                        margin: "7px 5px",
                    }
                },
                they: {
                    display: "flex",
                    justifyContent: "flex-start",
                    backgroundColor: "white",
                    borderRadius: "18.2px",
                    width: "auto",
                    padding: "5px 10px",
                    maxWidth: "64%",
                    color: "green",
                    margin: {
                        display: "flex",
                        justifyContent: "flex-start",
                        margin: "7px 5px"
                    }
                }
            }
        }
    }
    async componentDidMount () {
        //this.socket = await io(baseUrl);
        const inboxItems = await this.props.inbox ? this.props.inbox.inbox : []
        this.setState({
            followers: !this.props.followers ? [] : this.props.followers.followers,
            talks: inboxItems
        })

        this.props.socketConection.on("sendChat", data => {
            this.setUserState(data);
        })
    }
    componentWillUnmount(){
        let id = JSON.parse(localStorage.getItem('id'));
        this.props.socketConection.emit("removeuser", id);
    }
   
    //BUSCA UNA CONVERSACION ESPECIFICA CON EL ID. click chat columna izquierda
    inboxTalksHandler = async (e,{id, room}) => {
        e.preventDefault();
        var query = id
        let usuario = JSON.parse(localStorage.getItem('id'));
        let datos = {
            query,
            usuario,
            room
        }
        console.log("a", datos);
        this.setState({
            idTalk: query
        })
        this.props.socketConection.emit("fetchChat", datos);
    }


    //GUARDA AMIGO EN EL STATE. click amigo columna derecha
    inboxHandler = async (e, userid) => {
        e.preventDefault();
        var ids = userid
        let usuario = JSON.parse(localStorage.getItem('id'));
        let charla = await this.existChat(ids)
        let result = charla[0] ? true : false
        console.log("Wwwww", ids);
        //if exist a chat with these users fetch it
            if(result){
                let room = charla[0].room
                console.log("charlacharlacharla", result);
                let query = charla[0]._id
                let data = {
                    query, usuario, room
                }
                this.props.socketConection.emit("fetchChat", data);
            }
      
            this.setState({
                idTalk: "",
                room: ""
            })
    
            //set the information in the messege state
            let select = this.state.followers.filter(s => s.id._id === ids)
            let talkselect = this.state.talks === null ? [] : 
                                this.state.talks.filter(t => {//return complete chat with these conditions
                                return t.members.userOne._id === usuario && t.members.userTwo._id === ids ? 
                                        t.members.userOne._id === usuario && t.members.userTwo._id === ids : 
                                        t.members.userTwo._id === usuario && t.members.userOne._id === ids
            })
            this.setState({
                chatTitle: select,
                chatUsers: talkselect,
                room: talkselect[0] ? talkselect[0].room : ""
            })
            console.log("chattitle", select);

    }

    existChat = id =>{
        let userMain = JSON.parse(localStorage.getItem('id'));
        let frienid = id
        let findTalk = this.state.talks === null ? [] : 
                            this.state.talks.filter(t => {//return complete chat with these conditions
                            return t.members.userOne._id === userMain && t.members.userTwo._id === frienid ? 
                                    t.members.userOne._id === userMain && t.members.userTwo._id === frienid : 
                                    t.members.userTwo._id === userMain && t.members.userOne._id === frienid
        })
        return findTalk;
    }

    onAddItem = (data) => {
        const talk = data._id
        this.setState(state => {
            const iter = state.talks.some( i => i._id === talk)
            if(!iter){
          const talks = state.talks.concat(data);
          return {
            talks
          };
        }
        });
    }

    setUserState = (data) => {
        const localid = JSON.parse(localStorage.getItem('id'))
        const userOne = data.members.userOne
        const userTwo = data.members.userTwo
console.log("chattitle", data);
        this.setState({
            room: data.room,
            chatUsers: [data],
            chatTitle: [{
                id: {
                    _id: userOne._id === localid ? userTwo._id : userOne._id,
                    image: userOne._id === localid ? userTwo.image : userOne.image,
                    firstname: userOne._id === localid ? userTwo.firstname : userOne.firstname,
                    lastname: userOne._id === localid ? userTwo.lastname : userOne.lastname,
                    username: userOne._id === localid ? userTwo.username : userOne.username
                }
            }]
        })
        this.onAddItem(data);
    }
    //CREA UNA CONVERSACION NUEVA. recibe datos del formulario de envio
    handleSubmitInbox = (e) => {
        e.preventDefault();
        const talkId = this.state.chatUsers[0] ? this.state.chatUsers[0]._id : ''
        const RECEPTOR = this.state.chatTitle[0].id._id
        const EMISOR = JSON.parse(localStorage.getItem('id'))
        const roomSocket = this.state.room
        const datad = {
            contenido: {
                members: {
                    userOne: EMISOR,
                    userTwo: RECEPTOR
                },
                talk: {
                    author: EMISOR,
                    content: e.target.message.value
                }
            },
            talkId,
            roomSocket
        }
        e.target.message.value = ""
        this.props.socketConection.emit('sendMessage', datad)
    }


    //BUSCA LAS CONVERASCIONES DE EL USUARION
    // handleTalks = () => {
    //     const bearer = 'Bearer ' + localStorage.getItem('token');
    //     var config = {
    //         headers: { 'Authorization': bearer }
    //     };
    //     const QUERY = JSON.parse(localStorage.getItem('id'));
    //     fetch(baseUrl + `inbox-message/getch/${QUERY}`, config)
    //         .then(data => data.json())
    //         .then(json => {
    //             return this.state.talks === [] ? this.setState({ loading: true, talks: [] }) : this.setState({ loading: false, talks: json })
    //         })
    //         .catch(err => {
    //             this.setState({ loading: false, err: err })
    //         })

    // }

    render() {
        console.log("this.staet", this.state);
        console.log("this.state.talks", this.state.chatTitle);
        return (
         
                <div className=" grid-inbox">
                    <div className="grid-inbox-one">
                        <Chats talks={this.state.talks} inboxTalksHandler={this.inboxTalksHandler} />
                    </div>
                    <div className=" grid-inbox-two">
                        <Mensajes chatTitle={this.state.chatTitle} chatUsers={this.state.chatUsers}
                            handleSubmitInbox={this.handleSubmitInbox} chatStyle={this.state.style} />
                    </div>
                    <div className=" grid-inbox-three">
                        <AmigosComponent followers={this.state.followers} talks={this.state.talks} inboxHandler={this.inboxHandler} />
                    </div>
                </div>
           
        )
    }
}

export default RenderInbox;