import React from "react";

import Carousele from './HomeCarousel';
import {
  Button,
  Modal,
  ModalBody,
  ModalHeader,
  Form,
  FormGroup,
  Label,
  Input
} from "reactstrap";

class Home extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      isModalOpen: false
    };
    this.toggleModal = this.toggleModal.bind(this);

    this.handleLogin = this.handleLogin.bind(this);
  }

  toggleModal() {
    this.setState({
      isModalOpen: !this.state.isModalOpen
    });
  }

  handleLogin(event) {
    this.toggleModal();
    this.props.loginUser({
      username: this.username.value,
      password: this.password.value
    });

    event.preventDefault();
  }

  render() {
    return (
      <div className="container-fluid contenedor">
        <div className="elementos">
          <div className="col-12 col-md-7 carrusel" >
            <Carousele/>
          </div>
          <div className="col-12 col-md-5 detalles">
            <div className="fullSize intro">
              <h3>
                LandScapes <br/> Social Media
              </h3>
            </div>
            <div className="row botonera fullSize">
              <div className="col-sm-3 boton">
                <Button className=" btn btn-lg bg-success " onClick={this.toggleModal} >
                  Login 
                </Button>
              </div>
              <div className="col-sm-3 boton">
                <Button className="btn btn-lg bg-success " href="/users/signup">
                  Singup
                </Button>
              </div>
            </div>
          </div>
        </div>
        <div>
          <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
            <ModalHeader toggle={this.toggleModal}>Login</ModalHeader>
            <ModalBody>
              <Form onSubmit={this.handleLogin}>
                <FormGroup>
                  <Label htmlFor="username">Username</Label>
                  <Input
                    type="text"
                    id="username"
                    name="username"
                    innerRef={input => (this.username = input)}
                  />
                </FormGroup>
                <FormGroup>
                  <Label htmlFor="password">Password</Label>
                  <Input
                    type="password"
                    id="password"
                    name="password"
                    innerRef={input => (this.password = input)}
                  />
                </FormGroup>
                <FormGroup check>
                  <Label check>
                    <Input
                      type="checkbox"
                      name="remember"
                      innerRef={input => (this.remember = input)}
                    />
                    Remember me
                  </Label>
                </FormGroup>
                <Button type="submit" value="submit" color="primary">
                  Login
                </Button>
              </Form>
            </ModalBody>
          </Modal>
        </div>
      </div>
    );
  }
}
export default Home;
