import React, { useLayoutEffect, useState, useRef } from "react";
import { RenderComments } from './CommentComponent';
import { LikesModal } from './LikesModal';
import { Loading } from "./LoadingComponent";
import {
  Form,
  Label,
  Input,
  Button, Modal, ModalBody, ModalHeader, ModalFooter
} from "reactstrap";
import { Control, LocalForm } from 'react-redux-form';
import styled from 'styled-components'
import { baseUrl } from '../shared/baseUrl';
import { withRouter, Link } from "react-router-dom";

function useWindowSize() {
  const [size, setSize] = useState([0]);
  useLayoutEffect(() => {
    function updateSize() {
      setSize([window.innerWidth]);
    }
    window.addEventListener('resize', updateSize);
    updateSize();
    return () => window.removeEventListener('resize', updateSize);
  }, []);
  return size;
}

class Album extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      image: null,
      imageShowen: null,
      loadWall: null,
      isModalOpen: false,
      isTrashModalOpen: false,
      isLikesModalOpen: false,
      likes: [],
      i: '',
      modalType: null,
      arraySource: [],
      imgID: null,
      comentarios: []
    };
  }
  componentDidMount() {
    if (this.props.user.user) {
      let sourced = this.props.user.user.imagesWall.map(img => `${baseUrl}${img.filename}`)
      this.setState({
        arraySource: sourced
      });
    }
    this.props.socketConection.on("chatNotification", data => {
      console.log("llego el socket main", data);
      this.props.inboxFetch();
    })
  }

  controlState = (e) => {
    e.preventDefault();
    const target = e.target;
    const value = target.value;
    const name = target.name;

    this.setState({
      [name]: value
    });
  }

  handleimg = (e) => {
    e.preventDefault();
    let loadProfile = e.target.files[0];
    let showImg = URL.createObjectURL(e.target.files[0]);

    this.setState({
      image: loadProfile,
      imageShowen: showImg

    });
  }
  handleSubmit = (e) => {
    e.preventDefault();
    const filedata = new FormData();
    filedata.append('image', this.state.image, this.state.image.name);
    let ID = this.props.auth.user.username;
    this.props.imagenUser(ID, filedata)
      .then(() => {
        // this.props.history.push("/home");
        window.location.reload();
      });
  }

  //ENVIA LOS DATOS A REDUX PARA LAS IMAGENES DEL MURO
  //CARGA LA IMAGEN EN EL STATE DESDE EL FORM PARA LAS IMAGENS DEL MURO
  handlewallimg = (e) => {
    let loadingImgWall = e.target.files[0];
    this.setState({
      loadWall: loadingImgWall
    });
  }

  handleWallSubmit = (e) => {
    e.preventDefault();
    const fd = new FormData();
    fd.append('image', this.state.loadWall, this.state.loadWall.name);
    let ID = this.props.auth.user.username;
    console.log("aaa", fd);
    console.log("aaa", this.state.loadWall.name);
    this.props.imagenWall(ID, fd)
      .then(() => {
        //this.props.history.push("/home");
        window.location.reload();
      });
  }

  toggleModal = (e) => {
    let idfoto = e.target.alt
    let valueId = e.target.id
    if (valueId === "wall") {
      this.setState({
        isModalOpen: !this.state.isModalOpen,
        i: e.target.src,
        modalType: e.target.id,
        imgID: idfoto
      });
      this.fetchComments(idfoto);
      this.fetchImgLikes();
    } else if (valueId === "profile") {
      this.setState({
        isModalOpen: !this.state.isModalOpen,
        i: e.target.src,
        modalType: e.target.id,
        imgID: idfoto
      });
    } else if (valueId === "trashId") {
      this.setState({
        isTrashModalOpen: true
      });
    } else if (valueId === "likemodal") {
      this.setState({
        isLikesModalOpen: !this.state.isLikesModalOpen,
      });
    } else if (valueId === "trashIdOff") {
      this.setState({
        isTrashModalOpen: false,
      });
    }
    else {
      this.setState({
        isModalOpen: false,
        isTrashModalOpen: false,
        isLikesModalOpen: false
      });
    }

  }

  backForthModal = (e) => {
    let mode = e.target.id
    let currentlyUserId = JSON.parse(localStorage.getItem("id"))
    if (mode === "back") {
      let currentlyOne = this.state.i;
      let back = this.state.arraySource.indexOf(currentlyOne) - 1
      let elementBack = this.state.arraySource[back]
      this.setState({
        i: elementBack
      })
      this.fetchPhotoId(currentlyUserId, elementBack);
      this.fetchImgLikes();
    } else {
      let currentlyOne = this.state.i;
      let forth = this.state.arraySource.indexOf(currentlyOne) + 1
      let elementForth = this.state.arraySource[forth]
      this.setState({
        i: elementForth
      })
      this.fetchPhotoId(currentlyUserId, elementForth);
    }
  }

  deletePhotograph = () => {
    this.setState({ loading: true })
    const bearer = 'Bearer ' + localStorage.getItem('token');
    var config = {
      userid: JSON.parse(localStorage.getItem("id")),
      imageid: this.state.imgID
    }
    return fetch(baseUrl + `imagen/removeimage`, {
      method: "POST",
      body: JSON.stringify(config),
      headers: {
        'Authorization': bearer,
        "Content-Type": "application/json"
      }
    })
      .then(data => data.json())
      .then(json => {
        window.location.reload();
      })
      .catch(err => {
        this.setState({ loading: false, e: err })
      })
  }
  fetchPhotoId = (ID, element) => {
    let bodyPath = element.split(baseUrl)[1]
    var config = {
      foto: bodyPath
    };
    return fetch(baseUrl + `users/returnid/${ID}`, {
      method: "POST",
      body: JSON.stringify(config),
      headers: {
        "Content-Type": "application/json"
      }
    })
      .then(data => data.json())
      .then(response => {
        let result = response.result
        this.setState({ imgID: result })
        return result;
      })
      .then((result) => {
        this.fetchComments(result)
        this.fetchImgLikes();
      })
      .catch(err => {
        this.setState({ loading: false, e: err })
      })
  }

  fetchComments = (ID) => {
    return fetch(baseUrl + `comments/get-comments-image/${ID}`)
      .then(data => data.json())
      .then(json => {
        this.setState({
          comentarios: json
        })
      })
      .catch(err => {
        this.setState({ e: err })
      })
  }
  fetchImgLikes = async () => {
    let userId = await JSON.parse(localStorage.getItem("id"))
    let imgId = await this.state.imgID;
    const bearer = 'Bearer ' + localStorage.getItem("token");

    return fetch(baseUrl + `likes/get-i-like-it/${userId}/${imgId}`, {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        'Authorization': bearer
      },
      credentials: "same-origin" 
    })
      .then(data => data.json())
      .then(json => {
        this.setState({
          likes: json
        })
      })
      .catch(err => {
        this.setState({ e: err })
      })
  }
  deleteComments = (ID) => {
    const bearer = 'Bearer ' + localStorage.getItem('token');
    return fetch(baseUrl + `comments/get-comments-image/${ID}`, {
      method: "DELETE",
      headers: {
        'Authorization': bearer,
        "Content-Type": "application/json"
      }
    })
      .then(data => data.json())
      .then(json => {
        this.setState({
          comentarios: this.state.comentarios.filter(item => item._id !== json._id)
        });
      })
      .catch(err => {
        this.setState({ e: err })
      })
  }
  handleCommentSubmit = (values) => {

    const comment = {
      comment: values.comment,
      author: JSON.parse(localStorage.getItem("id")),
      image: this.state.imgID
    }
    const commentState = {
      comment: values.comment,
      author: this.props.user.user,
      image: this.state.imgID
    }
    this.props.commentsPost(comment)
      .then(() => {
        this.setState({
          comentarios: this.state.comentarios.concat(commentState)
        })
      })
  }

  render() {
    console.log("this.state.", this.state.loadWall);
    if (this.props.user.isLoading) {
      return (
        <div className="container">
          <div className="row">
            <Loading />
            <button onClick={this.handleFetch} />
          </div>
        </div>
      );
    } else if (this.props.user.errMess) {
      return (
        <div className="container">
          <div className="row">
            <h4>{this.props.user.errMess}</h4>
          </div>
        </div>
      );
    } else
      return (
        <div>

          <ImgProfile user={this.props.user.user}

          toggleModal={this.toggleModal}

          imageShowen={this.state.imageShowen}

            isModalOpen={this.state.isModalOpen} onSubmit={this.handleSubmit}
            source={this.state.i} handleimg={this.handleimg} imgProfileLoad={this.state.image}
            handleWallSubmit={this.handleWallSubmit} arraySource={this.state.arraySource} likes={this.state.likes}
            state={this.state} handlewallimg={this.handlewallimg} handleCommentSubmit={this.handleCommentSubmit}
            backForthModal={this.backForthModal} isTrashModalOpen={this.state.isTrashModalOpen} isLikesModalOpen={this.state.isLikesModalOpen}
            deletePhotograph={this.deletePhotograph} commentarios={this.state.comentarios} deleteComments={this.deleteComments}
          />
          <ImgWall imagesWall={this.props.user.user.imagesWall} handleWallSubmit={this.handleWallSubmit}
            state={this.state} handlewallimg={this.handlewallimg} toggleModal={this.toggleModal} />
        </div>
      )



  }
}

const ImgProfile = ({ user, toggleModal, imageShowen, isModalOpen, handleCommentSubmit,
  onSubmit, source, handleimg, arraySource, backForthModal, isTrashModalOpen, likes,
  handleWallSubmit, handlewallimg, state, deletePhotograph, commentarios, deleteComments, isLikesModalOpen }) => {
    
    let textInput = useRef(null);
    function handleClick() {
      textInput.current.click();
    }
    return (
    <div>
      <Modal isOpen={isModalOpen} toggle={toggleModal} size="lg" >
        <div style={isModalOpen && state.modalType === "wall" && arraySource.indexOf(source) !== 0 ?
          { color: "black", fontSize: "40px", position: "absolute", left: "-50px", top: "40%", zIndex: "1" } : { display: "none" }}>
          <span className="fas fa-angle-left" id="back" onClick={(event) => backForthModal(event)}></span>
        </div>
        <div style={isModalOpen && state.modalType === "wall" && arraySource.indexOf(source) !== arraySource.length - 1 ?
          { color: "", fontSize: "40px", position: "absolute", right: "-50px", top: "40%", zIndex: "1" } : { display: "none" }}>
          <span className="fas fa-angle-right" id="forth" onClick={(event) => backForthModal(event)}></span>
        </div>
        {state.modalType === "profile" ?
          <div className="modalprofile">
            <Form onSubmit={onSubmit} encType="multipart/form-data" >
              <LocalForm>
                <ModalBody toggle={toggleModal}>
                  <Label htmlFor="img">
                    <div className="imagina">
                      {imageShowen === null ?
                        <div className="hovertextfather" >
                          <h6 className="hovertext">Click For Change Your Photo</h6>
                        </div>
                        : null
                      }
                      <img src={imageShowen !== null ? imageShowen : source} alt="wall grid" className="w-100 h-100 mh-30 juajua" />
                    </div>
                    <Input
                      type="file"
                      id="img"
                      name="image"
                      className="btn-success"
                      onChange={handleimg}
                      style={{ display: "none" }}
                    />
                    <div className=" row foot" style={imageShowen !== null ? { display: "flex", alignItems: "center", justifyContent: "center" } : { display: "none" }}>
                      <Button type="submit" onClick={onSubmit} style={{ position: "absolute" }}>Change</Button>
                    </div>
                  </Label>
                </ModalBody>
              </LocalForm>
            </Form>
          </div>
          :
            <LocalForm onSubmit={(values) => handleCommentSubmit(values)}>
              <ModalBody toggle={toggleModal} className="modalWithoutPadding">
                <LikesModal isLikesModalOpen={isLikesModalOpen} toggle={toggleModal} likes={likes} />
                <Modalidad>
                  <div className="modalgroup">
                    <div className="c">
                      <div className="trashIcon">
                        <Button id="trashId" onClick={(e) => toggleModal(e)}><span className=" fa fa-trash-alt "></span></Button>
                      </div>
                      <img className="resp" src={source} alt="wall grid" />
                    </div>
                    <div className="modalComments">
                      <div className="comm">
                        <RenderComments comentarios={commentarios} deleteComments={deleteComments} />
                      </div>
                      <div>{
                        likes.length > 0
                          ?
                          <h6>Le gusta esta foto a <strong id="likemodal" onClick={(event) => toggleModal(event)} >{likes.length} usuarios</strong></h6>
                          :
                          <h6>Se el primero en marcar me gusta</h6>
                      }</div>
                      <div className="foot">
                        <div className="texto">
                          <Control.textarea model=".comment" id="comment" />
                        </div>
                        <div className="footButtons">
                          <div className="buton btn-sm" >
                            <Button type="submit" className="btn-sm"><span className="fas fa-paper-plane"></span></Button>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </Modalidad>
              </ModalBody>
            </LocalForm>
        }


        <ModalExample isTrashModalOpen={isTrashModalOpen} toggleModal={toggleModal} deletePhotograph={deletePhotograph} />
      </Modal>
      <div className="container ">
        <ProfileImage>
          <div className="image-content">
            <img
              src={baseUrl + user.image.filename}
              alt={user._id}
              value={imageShowen}
              id="profile"
              className="img-thumbnail"
              onClick={(e) => toggleModal(e)}
            />
          </div>
          

        </ProfileImage>


        <div className="user-info">
          <div className="user-info-description">
            <h5 className="">{user.firstname} {user.lastname}</h5>
            <h5 className="">{`"${user.phrase}"`}</h5>
            <h5 className="">{user.followers.length} Seguidores - {user.following.length} Seguidos</h5>
          </div>
          <div className="user-info-post">
            {state.loadWall === null ?
              <div>
                <input
                  type="file"
                  id="imgen"
                  name="image"
                  onChange={handlewallimg}
                  ref={textInput}
                  style={{ display: "none" }}
                />
                <button className="fa fa-camera fa-md" onClick={handleClick} />
              </div>
              : state.loadWall.name !== undefined ?
                <Form onSubmit={handleWallSubmit} encType="multipart/form-data" >
                  <LocalForm>
                    <Button onClick={handleWallSubmit}>Enviar</Button>
                  </LocalForm>
                </Form>
                :
                null
            }
          </div>
        </div>
      </div>
    </div>
  )
}
const ImgWall = ({ imagesWall, handleWallSubmit, handlewallimg, state, toggleModal }) => {
  const [width] = useWindowSize();
  let screen = width >= 600 ? true : false
    const WALL = 
      imagesWall && screen ?
        imagesWall.map((user) => {
      return (
        <div className="image-content" key={user._id}>
          <img className="img-thumbnail img-grid" id="wall" src={baseUrl + user.filename} alt={user._id} onClick={(e) => toggleModal(e)} />
        </div>
      )
      })
    :
    imagesWall.map((user) => {
      return (
        <div className="image-content" key={user._id}>
          <Link to={`/view/imagenwall/${user._id}`}>
            <img className="img-thumbnail img-grid" id="wall" src={baseUrl + user.filename} alt={user._id} onClick={(e) => toggleModal(e)} />
          </Link>
        </div>
      )
    })
  

  return (
    <div className="wall-container">
        {WALL}
     
    </div>

  )
}

const ModalExample = (props) => {


  return (
    <div>
      <Modal isOpen={props.isTrashModalOpen} toggle={props.toggleModal}>
        <ModalHeader >Delete Photograph</ModalHeader>
        <ModalBody toggle={props.toggleModal}>
          Do you want to DELETE this photograph?
        </ModalBody>
        <ModalFooter>
          <Button color="primary" onClick={props.deletePhotograph}>Delete Photo</Button>{' '}
          <Button color="secondary"  onClick={props.toggleModal}>Cancel</Button>
        </ModalFooter>
      </Modal>
    </div>
  );
}
const Modalidad = styled.div`
display: flex;
flex-direction: row;
justify-content: space-between;

div.modalgroup {
    display: flex;
flex-direction: row;
justify-content: space-between;
}
div.modalComments {
    display: flex;
flex-direction: column;
justify-content: space-between;

}

div.foot {
    display: flex;
flex-direction: row;
    margin-left: 5%;
}
div.footButtons {
    display: flex;
flex-direction: column;
}
div.comm {
    height: 460px;
    display: block;
    overflow-y: scroll;
  scroll-behavior: smooth;
}
div.c {
    width: 450px;
    height: 550px;
}
img.resp {

    width: 100%;
    height: 100%;
    max-width: auto;
    min-whidth: auto;
    object-fit: contain;
  }
  img.comment{
    margin-left: 5%;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    max-width: auto;
    min-whidth: auto;
    object-fit: cover;
  }
  @media (max-width: 991px) {
    div.modalComments {
      display: none;
    }
  }
`
const ProfileImage = styled.div`
display: flex;
flex-direction: row;
justify-content: center;
.image-content {
  width: 21vw;
  height: 21vw;
}


img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
}
`
export default withRouter(Album);
