import React from "react";

const PostSignup = props => {
  return (
    <div className="container">
      <div className="row">
        <h3 className="col-md-4 offset-4">{props.signup.successMess}</h3>
      </div>
      <div className="row">
        <a href="/" className="col-md-4 offset-4">
          <button className="btn-success col-md-4 offset-4">Ir a Home</button>
        </a>
      </div>
    </div>
  );
};

export default PostSignup;
